   }
}
@media screen and (min-width: 350px) and (max-width: 801px) {
   .container {
      margin: 0 20px;
   }
} */

.main {
  flex-grow: 1;
	margin: 20px 0px 0px 0px;
}

.App {
  height: 100%;
}
@media (max-width: 1330px) {
  .container {
    max-width: 1200px;
  }
}
@media (max-width: 992px) {
  .container {
    max-width: 750px;
  }
}
@media (max-width: 767px) {
  .container {
    max-width: none;
    margin: 0 20px;
  }
}
